// board
// ------------------------------------------

.board-column-wrapper {
  display: flex;
  height: 100%;
  overflow-x: auto;
  overflow-y: hidden;
}

.board-column {
  display: flex;
  flex-direction: column;
  width: 355px;
  min-width: 355px;
  padding: 8px;
  margin-right: 2rem;
  overflow-y: hidden;
}

.board-card-wrapper {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  overflow-y: auto;
}

.board-card {
  padding: 1rem;
  margin-top: .5rem;
  margin-bottom: .5rem;
  background: color("white");
  border-bottom: 2px solid rgba(0, 0, 0, .14);

  &:hover {
    background-color: color-a("white", .7);
  }

  .color-indicator {
    width: 1.2rem;
    min-width: 1.2rem;
    height: 1.2rem;
    border: 0;
  }
}

.board-card-desc {
  margin-bottom: 1rem;
  font-size: 14px;
  line-height: 1.8rem;
}
